<template>
    <div>
        <div ref="chatBox" class="chatBox fl" @scroll="scrollEvent" @load="chatload">
            聊天显示区
            <ul id="chat_box">
                <transition-group name="fade" enter-active-class="fadeIn"
                                leave-active-class="fadeOut"
                                :duration="200"
                            >
                    <li class='msg_line' v-for='(item) in chatArrays' :key="item.history">
                        <p class="head_img fr"><img src="@/pictures/1.jpg" width='30px' heigth='30px'></p>
                        <p class='msg_standard right fr'>
                            {{item.value}}
                        </p>
                        <div class='msg_line'>
                            <span class="dateTime">
                                {{item.time}}
                            </span>
                        </div>
                    </li>
                </transition-group>
            </ul>
        </div>
        <div ref="chatInput" class="chatInput fl" >
            <textarea class="inputArea" 
                      maxlength="50" wrap="hard" placeholder="请输入..." 
                      v-model="inputValue">
            </textarea>
        </div>
        <div ref="userList" class="userList fr" @scroll="scrollEvent">
            组队群组显示区
        </div>
        <div ref="userControl" class="userControl fr">
            <div class="controlButton">
                <button ref="RTC" class="RTC" @click="RTC">登出</button>
                <button ref="Team" class="Team" @click="Team"></button>
            </div>
            <div></div>
        </div>
    </div>
</template>
<script>
export default {
    name:"chatWindow",
    data() {
        return{
            inputValue: '',
            msg: '请输入',
            fixStyle: '',//自适应样式
            chatArrays: []
        }
    },
    mounted: function(){
        var _this = this;
        //变换浏览器窗口时改变显示大小
        window.onresize = () => {
        const windowWidth = document.body.clientWidth;
        const windowHeight = document.body.clientHeight;
        const windowAspectRatio = windowHeight / windowWidth
        let videoWidth
        let videoHeight
        if (windowAspectRatio < 0.5625) {
          videoWidth = windowWidth
          videoHeight = videoWidth * 0.5625
          this.fixStyle = {
            height: windowWidth * 0.5625 + 'px',
            width: windowWidth + 'px',
            'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
            'margin-left': 'initial',
          }
        } else {
          videoHeight = windowHeight
          videoWidth = videoHeight / 0.5625
          this.fixStyle = {
            height: windowHeight + 'px',
            width: windowHeight / 0.5625 + 'px',
            'margin-left': (windowWidth - videoWidth) / 2 + 'px',
            'margin-bottom': 'initial',
            }
          }
        }
        window.onresize();
    },
    methods:{
        chatload: function(){
            this.$emit('chat_complete',true);
        },
        scrollEvent: function (e) {
				console.log(e)
        },
        RTC: function(){
            this.$emit("backToLogin", 1);
            this.$notification.config({
                placement: 'bottomRight',
                bottom: '1vh',
                duration: 2,
              });
            this.$notification.open({
                message: '登出信息',
                description: '您已登出',
            });
        },
        Team: function(){

        },
        send_msg: function (Id) {
			var div = this.$refs.box;
		    var Value;
		    Value = this.inputValue;

		    let date = new Date()
      	    let y = date.getFullYear()
            let MM = date.getMonth() + 1
            MM = MM < 10 ? ('0' + MM) : MM
            let d = date.getDate()
            d = d < 10 ? ('0' + d) : d
            let h = date.getHours()
            h = h < 10 ? ('0' + h) : h
            let m = date.getMinutes()
            m = m < 10 ? ('0' + m) : m
            let s = date.getSeconds()
            s = s < 10 ? ('0' + s) : s
		    var Time = h + ':' + m + ':' + s
		    this.chat_num += 1;
		    var msg_obj = {
        	    id: Id,
        	    value: Value,
				time: Time,
				history: this.chat_num,
				}
			if(msg_obj.value === '') {}
			else { 
				this.chatArrays.push(msg_obj)
			}
			this.inputValue = ''
			setTimeout(()=>{
                div.scrollTop = div.scrollHeight;
            },0);
		    console.log(this.chatArrays)
	    },
    }
}
</script>
<style>
.chatBox{
    position: absolute;
    top: 0;
    left: 0;
	width: 70%;
	height: 49vh;
    line-height: 49vh;
    font-size: 3vh;
    text-align: center;
	overflow: auto;
	background-color: rgba(238, 238, 238, 0.74);
    border-radius: 5px 0 0 0;
    -moz-border-radius: 5px 0 0 0;
    -webkit-border-radius: 5px 0 0 0;
}
.chatInput{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 70%;
    height: 20vh;
    background-color: rgba(238, 238, 238, 0.74);;
    border-radius: 0 0 0 5px;
    -moz-border-radius: 0 0 0 5px;
    -webkit-border-radius: 0 0 0 5px;
    margin-top: 1vh;
}
.userList{
    position: absolute;
    top: 0;
    right: 0;
    width: 29%;
    height: 49vh;
    line-height: 49vh;
    font-size: 3vh;
    text-align: center;
    overflow: auto;
    background-color: rgba(238, 238, 238, 0.74);
    border-radius: 0 5px 0 0;
    -moz-border-radius: 0 5px 0 0;
    -webkit-border-radius: 0 5px 0 0;
}
.userControl{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 29%;
    height: 20vh;
    background-color: rgba(238, 238, 238, 0.74);
    border-radius: 0 0 5px 0;
    -moz-border-radius: 0 0 5px 0;
    -webkit-border-radius: 0 0 5px 0;
}
.controlButton{
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -30%;
    margin-top: -8vh;
    width: 60%;
    height: 16vh;
    background-color: rgb(248, 90, 90);
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
}
.RTC{
    position: absolute;
    left: 0;
    width: 49%;
    height: 16vh;
    line-height: 16vh;
    font-size: 3vh;
    text-align: center;
    background-color: rgb(248, 245, 90);
    border-radius: 5px 0 0 5px;
    -moz-border-radius: 5px 0 0 5px;
    -webkit-border-radius: 5px 0 0 5px;
}
.Team{
    position: absolute;
    right: 0;
    width: 49%;
    height: 16vh;
    background-color: rgb(95, 248, 90);
    border-radius: 0 5px 5px 0;
    -moz-border-radius: 0 5px 5px 0;
    -webkit-border-radius: 0 5px 5px 0;
}
.inputArea{
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -45%;
    margin-top: -9vh; 
    width: 90%; 
    height: 14vh; 
    background-color: rgba(65, 239, 245, 0.8);
    border-radius: 5px;
}
.msg_line{
	width: 100%;
	display: inline-block;
}
.head_img{
    position: relative;
    margin:5px 0px 5px 0px;
    right: 5px;
    border-radius: 5px;
}
.dateTime{
	line-height: 10px;
	font-size: 10px;
	background-color: whitesmoke;
	display: inline-block;
}
.fl{
	float: left;
}
.fr{
	float: right;
}
</style>



