<template>
  <div class="homepage-hero-module hidden">
    <Check v-if="isWarn" @callBack="IsWarn"></Check>
    <div v-else>
      <Loading v-if='loadding' :load='l_loadding'/>
      <transition name="fade" enter-active-class="fadeIn">
      <div v-show='!loadding'>
        <div id="header">
          各种链接
        </div>
        <transition mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut">
        <div class="video-container" style="background-color: black;">
          <transition-group mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut">
          <div v-show="!(isPass === 4)" key="login_1">  
            <div :style="fixStyle" class="filter"></div>
            <!-- 这里设置两个video的目的是消除高斯模糊后白边的出现 -->
              <video :style="fixStyle" muted autoplay loop class="bg_blur" @canplaythrough="canplay" 
                    @progress="V_progress">
                <source src="@/video/mp4/login.mp4" type="video/mp4"/>
              </video>
              <div class="v-loadding" v-show='v_loadding'>
                <a-spin style="margin-right: 5%;" size="large"/>
                <span>视频加载中...</span>
              </div>
              <div style="z-index: 0;" class="poster hidden" v-if="!vedioCanPlay">
                  <img :style="fixStyle" src="@/pictures/background/01.jpg" alt="" @load="preload(40)">
              </div>
          </div>
          <div v-show="isPass === 4" key="login_2">
            <img :style="fixStyle" src="@/pictures/background/04.jpg" alt="" @load="preload(40)">
          </div>
          </transition-group>
          
            <!-- 这部分是登陆窗口 -->
          <transition enter-active-class="fadeInLeftBig" leave-active-class="fadeOutLeftBig"> 
          <div class="loginBox" v-show="(isPass === 1)||(isPass === 2)">
            <div class="bg_blur" 
                 style="position: absolute;
                        width: 100%; 
                        height: 40vh;
                        background-color:rgba(50, 50, 50, 0.8);
                        z-index: -1;
                        -moz-border-radius: 15px;
                        -webkit-border-radius: 15px;
                        border-radius: 15px;
                 ">
            </div>
            <div>
              <div v-show="isPass === 1">
                <img :style="transfer" src="@/pictures/icon/logo.png" alt='' @load="preload(20)">
                <div>
                  <a-input class="inputStyle" placeholder="请输入用户名" v-model="id" ref="userInput" key="id" @keyup.enter="Next(1)">
                    <a-icon slot="prefix" type="user" key="id"/>
                    <a-icon v-if="id" slot="suffix" type="close-circle" @click="emitEmpty(1)" key="id"/>
                  </a-input>
                </div>
                <div>
                  <a-input class="inputStyle" placeholder="请输入密码" v-model="password" ref="passwordInput" key="password" @keyup.enter="Send">
                    <a-icon slot="prefix" type="unlock" key="password"/>
                    <a-icon v-if="password" slot="suffix" type="close-circle" @click="emitEmpty(2)" key="password"/>
                  </a-input>
                </div> 
                <a-button class="loginButton" ghost @click="Send">登陆</a-button>
                <a-button class="loginButton" ghost @click="Register">注册</a-button>
              </div>
            </div>
            <div v-show="isPass === 2">
              <div v-show="loadding_box">
                <a-spin class="Centre">
                  <a-icon slot="indicator" type="loading" style="font-size: 10vh;" spin />
                </a-spin>
              </div>
            </div>
          </div>
          </transition>
          <!--  -->
          <!-- 这部分是注册窗口 -->
          <transition-group enter-active-class="fadeInRightBig" leave-active-class="fadeOutRightBig">
          <div class="registerBox" v-show="isPass === 3" key="page_1">
            <div class="bg_blur" 
                 style="position: absolute;
                        width: 100%; 
                        height: 60vh;
                        background-color:rgba(50, 50, 50, 0.8);
                        z-index: -1;
                        -moz-border-radius: 15px;
                        -webkit-border-radius: 15px;
                        border-radius: 15px;
                 ">
            </div>
            <div>
              <div>
                <a-input class="inputStyle" placeholder="请输入用户名" v-model="re_id" ref="re_userInput" key="id" @keyup.enter="reNext(1)">
                  <a-icon slot="prefix" type="user" key="id"/>
                  <a-icon v-if="re_id" slot="suffix" type="close-circle" @click="re_emitEmpty(1)" key="id"/>
                </a-input>
              </div>
              <div>
                <a-input class="inputStyle" placeholder="请输入密码" v-model="re_password" ref="re_passwordInput" key="password" @keyup.enter="reNext(2)">
                  <a-icon slot="prefix" type="unlock" key="password"/>
                  <a-icon v-if="re_password" slot="suffix" type="close-circle" @click="re_emitEmpty(2)" key="password"/>
                </a-input>
              </div> 
              <div>
                <a-input class="inputStyle" placeholder="请输入Origin账号" v-model="re_origin" ref="re_originInput" key="origin_id" @keyup.enter="reNext(3)">
                  <a-icon slot="prefix" type="paper-clip" key="origin_id"/>
                  <a-icon v-if="re_origin" slot="suffix" type="close-circle" @click="re_emitEmpty(3)" key="origin_id"/>
                </a-input>
              </div>
              <div>
                <a-input class="inputStyle" placeholder="请输入邮箱 ( 用于找回密码 )" v-model="re_mail" ref="re_mailInput" key="re_mail" @keyup.enter="reNext(4)">
                  <a-icon slot="prefix" type="mail" key="re_mail"/>
                  <a-icon v-if="re_mail" slot="suffix" type="close-circle" @click="re_emitEmpty(4)" key="re_mail"/>
                </a-input>
              </div>
              <div>
                <a-input class="inputStyle" placeholder="请输入电话号码" v-model="re_phone" ref="re_phoneInput" key="re_phone" @keyup.enter="reServer">
                  <a-icon slot="prefix" type="phone" key="re_phone"/>
                  <a-icon v-if="re_phone" slot="suffix" type="close-circle" @click="re_emitEmpty(5)" key="re_phone"/>
                </a-input>
              </div>
              <div style="margin-top: 2%;">
                <a-button class="registerButton" ghost @click="reServer">注册</a-button>
                <a-button class="registerButton" ghost @click="Back">返回</a-button>
              </div>
            </div>
          </div>
          <div v-show="isPass === 4" class="chatWindow" key="page_2">
            <chatWindow  @chat_complete="chatComplete" @backToLogin="BackToLogin"></chatWindow>
          </div>
          </transition-group>
          <!--  -->
        </div>
        </transition>
      </div>
      </transition>
    </div>
  </div>
</template>
<script>
import { setTimeout } from 'timers';

export default {
    name: 'login',
    data() {
        return{
            loadding: true,//页面加载标识
            isWarn: true,//浏览器版本警告标识
            vedioCanPlay: false,//背景视频播放标识
            v_loadding: true,//背景视频加载标识是否显示
            l_loadding: 0,//登陆页面图片加载进度 
            chat_complete: false,//聊天页面资源是否加载完成
            fixStyle: '',//自适应样式
            id: '',//登陆窗口用户名
            password: '',//登陆窗口密码
            re_id: '',//注册用户名
            re_password: '',//注册密码
            re_origin: '',//注册关联的origin账号
            re_mail: '',//注册邮箱
            re_phone: '',//注册电话
            transfer: {
              'display': 'inline-block', 
              'width': '20%',
              'margin-top': '5%',
            },
            isPass: 1,//登陆窗口切换标识 1:登陆窗口 2:加载中 3:注册窗口 4:聊天页面
            loadding_box: true,//加载中标识
        }
    },
    mounted: function(){
        var _this = this;
        //变换浏览器窗口时改变显示大小
        window.onresize = () => {
        const windowWidth = document.body.clientWidth;
        const windowHeight = document.body.clientHeight;
        const windowAspectRatio = windowHeight / windowWidth
        let videoWidth
        let videoHeight
        if (windowAspectRatio < 0.5625) {
          videoWidth = windowWidth
          videoHeight = videoWidth * 0.5625
          this.fixStyle = {
            height: windowWidth * 0.5625 + 'px',
            width: windowWidth + 'px',
            'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
            'margin-left': 'initial',
          }
        } else {
          videoHeight = windowHeight
          videoWidth = videoHeight / 0.5625
          this.fixStyle = {
            height: windowHeight + 'px',
            width: windowHeight / 0.5625 + 'px',
            'margin-left': (windowWidth - videoWidth) / 2 + 'px',
            'margin-bottom': 'initial',
            }
          }
        }
        window.onresize();
    },
    watch:{
      //监听加载完成
      "l_loadding": function(){
        var _this = this;
        if(this.l_loadding === 100){
          setTimeout(
            function(){
              _this.loadding = false;
            },
            1000
          );
        }
      },
      //聊天页面加载完成触发
      "chat_complete": function(){
        var _this = this;
        if(this.chat_complete && (this.isPass === 2)){
          _this.isPass = 4;
          this.$notification.config({
            placement: 'bottomRight',
            bottom: '1vh',
            duration: 2,
          });
          this.$notification.open({
            message: '登陆信息',
            description: '欢迎您，亲爱的xxx',
          });
        }
      }
    },
    methods: {
        //登陆页面加载标识
        preload: function(e){
          var _this = this;
          var num = this.l_loadding;
          for(var i = num; i < num + e; i++){
            setTimeout(
              function(){
                _this.l_loadding += 1;
              },100
            );
          }
        },
        //控制背景视频播放的方法，当播放出现问题显示静态图片
        canplay: function() {
            this.vedioCanPlay = true;
        },
        //发送登陆信息
        Send: function(){
          var _this = this;
          this.isPass = 2;
          setTimeout(function(){
            _this.isPass = 4;
          },1000);
        },
        //切换至注册页面
        Register: function(){
          this.isPass = 3;
        },
        //清空输入框中的值
        emitEmpty: function(num) {
          if (num === 1){
            this.$refs.userInput.focus()
            this.id = ''
          }else{
            this.$refs.passwordInput.focus()
            this.password = ''
          }
        },
        //返回清空注册页面的信息
        re_emitEmpty: function(num){
          if (num === 1){
            this.$refs.re_userInput.focus()
            this.re_id = ''
          }else if(num === 2){
            this.$refs.re_passwordInput.focus()
            this.re_password = ''
          }else if(num === 3){
            this.$refs.re_originInput.focus()
            this.re_origin = ''
          }else if(num === 4){
            this.$refs.re_mailInput.focus()
            this.re_mail = ''
          }else{
            this.$refs.re_phoneInput.focus()
            this.re_phone = ''
          }
        },
        //登陆页面跳转至下一个输入框
        Next: function(num){
          if(num === 1){
            this.$refs.passwordInput.focus();
          }
        },
        //注册页面跳转至下一个输入框
        reNext: function(num){
          if(num === 1){
            this.$refs.re_passwordInput.focus();
          }else if(num === 2){
            this.$refs.re_originInput.focus();
          }else if(num === 3){
            this.$refs.re_mailInput.focus();
          }else if(num === 4){
            this.$refs.re_phoneInput.focus();
          }
        },
        //注册信息发送
        reServer: function(){

        },
        //注册页面返回登陆页面
        Back: function(){
          this.isPass = 1;
          this.re_id = '';
          this.re_password = '';
          this.re_origin = '';
          this.re_mail = '';
          this.re_phone = '';
        },
        //确认聊天界面加载成功
        chatComplete: function(e){
          this.chat_complete = e;
        },
        //是否进行浏览器检测警告
        IsWarn: function(e){
          this.isWarn = e;
        },
        BackToLogin: function(e){
          this.isPass = e;
        },
        V_progress: function(){
          var _this = this;
          this.v_loadding = true; 
          setTimeout(function(){
            _this.v_loadding = false;
          }, 1000);
        }
    }
}
</script>
<style>
body{
  padding: 0;
  margin: 0;
  height: 100%;
  overflow: hidden;
  background-size: 150% 150%;
}

#header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8vh;
  line-height: 8vh;
  font-size: 2vh;
  text-align: center;
  color: white;
  background-color: rgb(73, 73, 73);
  box-shadow: 0px 2px 1px rgb(73, 73, 73);
  z-index: 10;
}

.loginBox{
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -10%;
  margin-top: -20vh;
  width: 20%;
  height: 40vh;
  -moz-border-radius: 15px;
  -webkit-border-radius: 15px;
  border-radius: 15px;
  background-color:rgba(200, 200, 200, 0.9);
  z-index: 10;
  text-align: center;
}

.registerBox{
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -20%;
  margin-top: -30vh;
  width: 40%;
  height: 60vh;
  -moz-border-radius: 15px;
  -webkit-border-radius: 15px;
  border-radius: 15px;
  background-color:rgba(200, 200, 200, 0.9);
  z-index: 10;
  text-align: center;
}

.chatWindow{
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -30%;
  margin-top: -35vh;
  width: 60%;
  height: 70vh;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  background-color:rgba(0, 0, 0, 0.8);
  z-index: 10; 
}

.homepage-hero-module,
.video-container
{
  position: relative;
  height: 100vh;
}

.video-container .poster img,
.video-container video 
{
  position: absolute;
  z-index: 0;
}

.video-container .filter 
{
  z-index: 1;
  position: absolute;
  background: rgba(0, 0, 0, 0.1);
}

.bg_blur{
  -webkit-filter: blur(1px);
  -ms-filter: blur(1px);
  -o-filter: blur(1px);
  filter: blur(1px);
}

.inputStyle{
  display: inline-block;
  width: 80%;
  height: 10%;
  font-size: 10%;
  margin-top: 5%;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.788)
}

.loginButton{
  display: inline-block;
  width: 60%;
  height: 10%;
  margin-top: 5%;
}

.registerButton{
  display: inline-block;
  width: 60%;
  height: 8%;
  margin-top: 2%;
}

.v-loadding{
  position: absolute;
  width: 10%;
  height: 8vh;
  line-height: 8vh;
  text-align: center;
  bottom: 2vh;
  left: 2%;
  color: rgba(255, 255, 255);
  background-color: rgb(73, 73, 73);
  border-radius: 15px;
  z-index: 10;
}

.Centre{
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -5vh;
  margin-top: -5vh;
}

.hidden{
  overflow: hidden;
}
</style>



